.ap {
    display: inline-flex;
    height: 20px;
    width: 20px;
    background-image: url('../sheet_apple_16_optimized.png');
    background-size: 820px;
    background-repeat: no-repeat;
    text-indent: -999px;
    box-sizing: border-box;
    font-size: 20px;
    line-height: 1;
    overflow: hidden;
    margin-top: -3px;
}

